<template>
<div :style='{"width":"1200px","padding":"120px 7% 100px","margin":"0px auto","position":"relative","background":"url(http://codegen.caihongy.cn/20230109/138a4d606eae4a168810cef229969885.png) no-repeat center top,url(http://codegen.caihongy.cn/20230109/b95aeae1c1294f398aefb7c6cd5b16de.png) no-repeat center top,url(http://codegen.caihongy.cn/20230109/5ffc803e6682418eb7f0b09a98e35527.png) no-repeat center bottom,#faf0e6"}'>
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="110px"
    >
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}'  label="月份" prop="yuefen">
            <el-select v-model="ruleForm.yuefen" placeholder="请选择月份"  >
              <el-option
                  v-for="(item,index) in yuefenOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="员工工号" prop="yuangonggonghao">
            <el-select  @change="yuangonggonghaoChange" v-model="ruleForm.yuangonggonghao" placeholder="请选择员工工号">
              <el-option
                  v-for="(item,index) in yuangonggonghaoOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="员工姓名" prop="yuangongxingming">
            <el-input v-model="ruleForm.yuangongxingming" 
                placeholder="员工姓名" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="图片" v-if="type!='cross' || (type=='cross' && !ro.tupian)" prop="tupian">
            <file-upload
            tip="点击上传图片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.tupian?ruleForm.tupian:''"
            @change="tupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' class="upload" v-else label="图片" prop="tupian">
                <img v-if="ruleForm.tupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="部门" prop="bumen">
            <el-input v-model="ruleForm.bumen" 
                placeholder="部门" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="手机号" prop="shoujihao">
            <el-input v-model="ruleForm.shoujihao" 
                placeholder="手机号" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="岗位" prop="gangwei">
            <el-input v-model="ruleForm.gangwei" 
                placeholder="岗位" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="基本工资" prop="jibengongzi">
            <el-input v-model="ruleForm.jibengongzi" 
                placeholder="基本工资" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="全勤奖励" prop="quanqinjiangli">
            <el-input v-model="ruleForm.quanqinjiangli" 
                placeholder="全勤奖励" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="其他补助" prop="qitabuzhu">
            <el-input v-model="ruleForm.qitabuzhu" 
                placeholder="其他补助" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="扣款事项" prop="koukuanshixiang">
            <el-input v-model="ruleForm.koukuanshixiang" 
                placeholder="扣款事项" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="扣款金额" prop="koukuanjine">
            <el-input v-model="ruleForm.koukuanjine" 
                placeholder="扣款金额" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="实发工资" prop="shifagongzi">
              <el-input v-model="shifagongzi" placeholder="实发工资" readonly></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","boxShadow":"0 2px 0 #efe4d4","margin":"0 0 10px"}' label="登记时间" prop="dengjishijian" >
              <el-date-picker
                  format="yyyy 年 MM 月 dd 日"
                  value-format="yyyy-MM-dd"
                  v-model="ruleForm.dengjishijian" 
                  type="date"
                  placeholder="登记时间">
              </el-date-picker> 
          </el-form-item>

      <el-form-item :style='{"padding":"0","textAlign":"center","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"4px","background":"#cca162","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"0px solid rgba(64, 158, 255, 1)","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"4px","background":"#009688","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        baseUrl: '',
        ro:{
            yuefen : false,
            yuangonggonghao : false,
            yuangongxingming : false,
            tupian : false,
            bumen : false,
            shoujihao : false,
            gangwei : false,
            jibengongzi : false,
            quanqinjiangli : false,
            qitabuzhu : false,
            koukuanshixiang : false,
            koukuanjine : false,
            shifagongzi : false,
            dengjishijian : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          yuefen: '',
          yuangonggonghao: '',
          yuangongxingming: '',
          tupian: '',
          bumen: '',
          shoujihao: '',
          gangwei: '',
          jibengongzi: '',
          quanqinjiangli: '',
          qitabuzhu: '',
          koukuanshixiang: '',
          koukuanjine: '',
          shifagongzi: '',
          dengjishijian: '',
        },
        yuefenOptions: [],
        yuangonggonghaoOptions: [],
        rules: {
          yuefen: [
          ],
          yuangonggonghao: [
          ],
          yuangongxingming: [
          ],
          tupian: [
          ],
          bumen: [
          ],
          shoujihao: [
          ],
          gangwei: [
          ],
          jibengongzi: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          quanqinjiangli: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          qitabuzhu: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          koukuanshixiang: [
          ],
          koukuanjine: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          shifagongzi: [
          ],
          dengjishijian: [
          ],
        },
      };
    },
    computed: {
        shifagongzi: {
            get: function () {
                return 0+parseFloat(this.ruleForm.jibengongzi==""?0:this.ruleForm.jibengongzi)+parseFloat(this.ruleForm.quanqinjiangli==""?0:this.ruleForm.quanqinjiangli)+parseFloat(this.ruleForm.qitabuzhu==""?0:this.ruleForm.qitabuzhu)-parseFloat(this.ruleForm.koukuanjine==""?0:this.ruleForm.koukuanjine) || 0
            }
        },



    },
    created() {
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='yuefen'){
              this.ruleForm.yuefen = obj[o];
              this.ro.yuefen = true;
              continue;
            }
            if(o=='yuangonggonghao'){
              this.ruleForm.yuangonggonghao = obj[o];
              this.ro.yuangonggonghao = true;
              continue;
            }
            if(o=='yuangongxingming'){
              this.ruleForm.yuangongxingming = obj[o];
              this.ro.yuangongxingming = true;
              continue;
            }
            if(o=='tupian'){
              this.ruleForm.tupian = obj[o].split(",")[0];
              this.ro.tupian = true;
              continue;
            }
            if(o=='bumen'){
              this.ruleForm.bumen = obj[o];
              this.ro.bumen = true;
              continue;
            }
            if(o=='shoujihao'){
              this.ruleForm.shoujihao = obj[o];
              this.ro.shoujihao = true;
              continue;
            }
            if(o=='gangwei'){
              this.ruleForm.gangwei = obj[o];
              this.ro.gangwei = true;
              continue;
            }
            if(o=='jibengongzi'){
              this.ruleForm.jibengongzi = obj[o];
              this.ro.jibengongzi = true;
              continue;
            }
            if(o=='quanqinjiangli'){
              this.ruleForm.quanqinjiangli = obj[o];
              this.ro.quanqinjiangli = true;
              continue;
            }
            if(o=='qitabuzhu'){
              this.ruleForm.qitabuzhu = obj[o];
              this.ro.qitabuzhu = true;
              continue;
            }
            if(o=='koukuanshixiang'){
              this.ruleForm.koukuanshixiang = obj[o];
              this.ro.koukuanshixiang = true;
              continue;
            }
            if(o=='koukuanjine'){
              this.ruleForm.koukuanjine = obj[o];
              this.ro.koukuanjine = true;
              continue;
            }
            if(o=='shifagongzi'){
              this.ruleForm.shifagongzi = obj[o];
              this.ro.shifagongzi = true;
              continue;
            }
            if(o=='dengjishijian'){
              this.ruleForm.dengjishijian = obj[o];
              this.ro.dengjishijian = true;
              continue;
            }
          }
        }
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
          }
        });
        this.yuefenOptions = "一月,二月,三月,四月,五月,六月,七月,八月,九月,十月,十一月,十二月".split(',')
        this.$http.get('option/yuangong/yuangonggonghao', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.yuangonggonghaoOptions = res.data.data;
          }
        });
      },
      // 下二随
      yuangonggonghaoChange () {
        this.$http.get('follow/yuangong/yuangonggonghao?columnValue=' + this.ruleForm.yuangonggonghao, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            if(res.data.data.yuangongxingming){
              this.ruleForm.yuangongxingming = res.data.data.yuangongxingming
            }
            if(res.data.data.bumen){
              this.ruleForm.bumen = res.data.data.bumen
            }
            if(res.data.data.shoujihao){
              this.ruleForm.shoujihao = res.data.data.shoujihao
            }
            if(res.data.data.gangwei){
              this.ruleForm.gangwei = res.data.data.gangwei
            }
          }
        });
      },

    // 多级联动参数
      // 多级联动参数
      info(id) {
        this.$http.get('xinchoujilu/detail/${id}', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {

          this.ruleForm.shifagongzi = this.shifagongzi
        //更新跨表属性
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('xinchoujilu/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 跨表计算


                          this.$http.post('xinchoujilu/add', this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '操作成功',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {


                  this.$http.post('xinchoujilu/add', this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '操作成功',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // 获取uuid
      getUUID () {
        return new Date().getTime();
      },
      // 返回
      back() {
        this.$router.go(-1);
      },
      tupianUploadChange(fileUrls) {
          this.ruleForm.tupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
    }
  };
</script>

